:root {
    --surface-color: #fff;
    --curve: 40;
  }
  
  * {
    box-sizing: border-box;
  }
  
 
  .cards {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    grid-gap: 2rem;
    gap: 2rem;
    margin: 4rem 5vw;
    padding: 0;
    list-style-type: none;
  }
  @media only screen and (min-width: 747px) {
    .saclead{
      left: 15vw;
    }
  }
  
  
  .card {
    position: relative;
    display: block;
    height: 100%;  
    border-radius: calc(40 * 1px);  
    border-radius: calc(var(--curve) * 1px);
    overflow: hidden;
    text-decoration: none;
  }
  .card1 {
    position: relative;
    display: block;
    height: 100%;  
    border-radius: calc(40 * 1px);  
    border-radius: calc(var(--curve) * 1px);
    overflow: hidden;
    text-decoration: none;
  }

  
  .card__image {      
    width: 100%;
    height: auto;
  }
  
  
  .card__overlay {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 1;      
    border-radius: calc(40 * 1px);      
    border-radius: calc(var(--curve) * 1px);    
    background-color: #fff;    
    background-color: var(--surface-color);      
    -webkit-transform: translateY(100%);      
            transform: translateY(100%);
    transition: .2s ease-in-out;
  }
  
  .card:hover .card__overlay {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  
  .card__header {
    position: relative;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    grid-gap: 2em;
    gap: 2em;
    padding: 2em;
    border-radius: calc(40 * 1px) 0 0 0;
    border-radius: calc(var(--curve) * 1px) 0 0 0;    
    background-color: #fff;    
    background-color: var(--surface-color);
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
    transition: .2s ease-in-out;
  }
  
  .card__arc {
    width: 80px;
    height: 80px;
    position: absolute;
    bottom: 100%;
    right: 0;      
    z-index: 1;
  }
  
  .card__arc path {
    fill: #fff;
    fill: var(--surface-color);
    d: path("M 40 80 c 22 0 40 -22 40 -40 v 40 Z");
  }       
  
  .card:hover .card__header {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  
  .card__thumb {
    -webkit-flex-shrink: 0;
            flex-shrink: 0;
    width: 50px;
    height: 50px;      
    border-radius: 50%;      
  }
  
  .card__title {
    font-size: 1em;
    margin: 0 0 .3em;
    color: #6A515E;
  }
  
  .card__tagline {
    display: block;
    margin: 1em 0;
    font-family: "MockFlowFont";  
    font-size: .8em; 
    color: #5ab2ff;  
  }
  
  .card__status {
    font-size: .8em;
    color: #5ab2ff;  
  }
  
  .card__description {
    padding: 0 2em 2em;
    margin: 0;
    color: #5ab2ff;  
    font-family: "MockFlowFont";   
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 3;
    overflow: hidden;
  }    

